.webform-contactus{
    
    .form-wrap{
        margin-top: 67px;
        label{
            @include banner-para;
            color: $black;
        }
        input[type='email'],
        input[type='text']{
            border-top:0 ;
            border-left: 0;
            border-right:0;
            padding-top: 24px;
            padding-bottom: 12px;
            padding-left: 0;
            border-radius: 0;
            border-bottom: 1px solid $lightgray;
        }
        .form-control{
            &:focus{
                box-shadow: none;
                border-color: $lightgray;
            }
        }
        .form-select{
           border:1px solid $lightgray ;
            padding: 18px 20px;
        &:focus{
            box-shadow: none;
            border-color: $lightgray;

        }
        }
        .form-textarea-wrapper{
            margin-top: 20px;
            .form-control{
                border:1px solid $lightgray ;
            }
        }
        .form-item-accept-terms-conditions{
            label{
                padding-top: 4px;
                padding-left: 15px;
               
            }
        }
        .form-item-info-text{
            margin-top: 20px;
        }
        .webform-button--submit {
            background: $black;
            padding: 10px 60px;
            width: auto;
            @include explore-link ;
            border-radius: 7px;
            
        }

    }
}