/* User login links */
.user-logged-in {
  display: block;

  .nav-link--user-login {
    display: none;
  }
}

.header {
  box-shadow: 0px 4px 16px 0px $dropshadow;

  .affix {
    background: $white;
  }

  section {
    &.row {
      &.region {
        &.region-top-header {
          width: 100%;
          margin: 0 auto;
        }
      }
    }
  }
}


.menu--account {
  ul {
    li {
      padding: 0 25px;
      position: relative;
    }

    a.nav-link.search {
      background: url('../images/search.png') no-repeat 0px 3px;
      font-size: 0;
      width: 66px;
      height: 66px;

      &:before {
        border-bottom: 0;
      }
    }

    a.nav-link {
      position: relative;
      color: $black;
      @include main-menu-font;
      line-height: 66px;
      padding: 0;

      &:hover {
        color: $black;
        text-decoration: none;
      }
    }
  }
}

.navbar,
.navbar-brand {
  padding: 0;

}
.navbar-brand {
  img{
    width:300px;
  }
}

#CollapsingNavbar {
  margin-left: auto;
}

